export default {
	data() {
		return {
			centerTItle:'',
			description:''
		}
	},
	onLoad(e) {
		var id = e.id
		this.newsCenter(id)
	},
	methods: {
		async newsCenter(sid){
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/messageDetail?id=" + sid
			})
			that.centerTItle = res.data.data.title
			that.description = res.data.data.description
		}
	}
}
